<script>
import MainBanner from '../components/MainBanner'
import axios from 'axios'
import moment from 'moment'

const today = new Date();

export default {
  name: 'HomeView',
  components: {
    MainBanner
  },
  data() {
    return {
      concurso: null,
      data: null,
      dezenas: null,
      premiacoes: null,
      acumulou: null,
      acumuladaProxConcurso: null,
      dataProxConcurso: null,
      today: moment(today).format("DD/MM/YYYY")
    }
  },
  mounted() {
    axios.get('https://loteriascaixa-api.herokuapp.com/api/mega-sena/latest')
    .then(response => (
      this.concurso = response.data.concurso, 
      this.data = response.data.data, 
      this.dezenas = response.data.dezenas,
      this.premiacoes = response.data.premiacoes, 
      this.acumulou = response.data.acumulou,
      this.acumuladaProxConcurso = response.data.acumuladaProxConcurso, 
      this.dataProxConcurso = response.data.dataProxConcurso
      )
    )
  }
}
